import { ActionsType, Model, fetch, getPayload } from '@topwrite/common';
import { history } from '@/lib/history';

interface PageState {
    loading: boolean
    error?: Error
}

class Page extends Model<PageState> {

    initialState = {
        loading: false
    };

    subscription({ fetch }: ActionsType<Page>) {
        let prevLocation = history.location;
        history.listen(async ({ location }) => {
            if (prevLocation.pathname !== location.pathname) {
                fetch(location.pathname);
            }
            prevLocation = location;
        });
    }

    async *fetch(pathname: string) {
        yield this.setState(state => {
            state.loading = true;
        });

        try {
            return await fetch(decodeURI(pathname)).then((response) => {
                if (response.status !== 204
                    && response.headers.has('Content-Type')
                    && response.headers.get('Content-Type')!.match(/application\/json/i)
                ) {
                    return response.json();
                } else {
                    return response.text().then(html => getPayload(html));
                }
            });
        } catch (e) {
            yield this.setState(state => {
                state.error = e;
            });
        } finally {
            yield this.setState(state => {
                state.loading = false;
            });
        }
    }
}

export const page = new Page();
